* {
  margin: 0;
  padding: 0;
}

:focus {
  outline: none;
}

body {
  scroll-behavior: smooth;
}

img {
  line-height: 0;
  display: block;
}

a {
  text-decoration-line: none;
}

ul {
  list-style-type: none;
}

button {
  cursor: pointer;
  background-color: #0000;
  border: none;
  display: block;
}

* {
  transition: all .2s cubic-bezier(.21, .24, .58, .98);
}

html {
  scroll-behavior: smooth;
}

.main {
  background-color: #282828;
  display: flex;
}

.wrap {
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

.room {
  background-color: #444;
  background-image: url("https://i.postimg.cc/Pq4T9SWb/floar-1.png"), url("https://i.postimg.cc/rmDy4S4S/wall-1.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  width: 983px;
  height: 653px;
  position: relative;
}

.room__img {
  width: max-content;
  position: absolute;
  transform: translate(-50%, -50%);
}

.sidebar {
  background-color: #313131;
  width: 400px;
  min-width: 400px;
  height: 100vh;
  padding: 40px;
  display: block;
  overflow: auto;
}

.sidebar::-webkit-scrollbar {
  display: none;
}

.sidebar__list {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.sidebar__item {
  background-color: #282828;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: 150px;
  display: flex;
}

.sidebar__img {
  object-fit: contain;
  width: 120px;
  height: 120px;
}
